@import url(https://fonts.googleapis.com/css?family=Roboto:500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*
* Prefixed by https://autoprefixer.github.io
* PostCSS: v8.3.6,
* Autoprefixer: v10.3.1
* Browsers: last 4 version
*/
body {
    margin: 1rem;
    font-family: 'Roboto', sans-serif;
}
.App {
    text-align: center;
}
.App * {
    box-sizing: border-box;
}
ul {
    padding: 0;
    list-style: none;
}
p {
    margin: 5px 0;
}
.nav {
    display: flex;
    background-color: white;
    margin: 1rem;
}
.logo {
    max-width: 150px 
}
.display-none {
    display: none;
}
.showSuggestions {
    display: block;
}
input[type="text"], input[type="number"] {
    padding: 8px 6px 8px 10px;
    border-radius: 20px;
    outline: none;
    border: 1.75px solid #ccc;
    font-size: 14px;
    line-height: 20px;
    -ms-grid-row-align: center;
    align-self: center;
    height: 38px;
}
input[type="text"]:focus, input[type="number"]:focus {
    outline: none;
    border: none;
    box-shadow: 0px 0px 0px 2.5px blue;
    background-color: rgb(250, 250, 250);
}
input:focus::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0);
}
input:focus:-ms-input-placeholder {
    color: rgba(255, 255, 255, 0);
}
input:focus::placeholder {
    color: rgba(255, 255, 255, 0);
}
.city {
    width: 250px;
}
.CitySearch {
    display: flex;
    flex-direction: column;
    flex-basis: 300px;
    max-width: 300px;
    margin: auto;
    position: relative;
}
.suggestions {
    width: 250px;
    position: absolute;
    z-index: 2;
    -ms-grid-row-align: center;
    align-self: center;
    margin: 0;
    top: 43px;
    font-size: 14px;
    box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}
.suggestions > li {
    text-align: left;
    padding: 8px 6px 8px 10px;
    background-color: #eee;
    cursor: default;
}
.suggestions > li:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}
.suggestions > li:hover {
    background-color: rgb(212, 212, 212);
    color: black;
}
#event-title {
  font-size: 3rem;
  margin: 1rem 0;
}
.EventList {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 1fr 2rem 1fr 2rem 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    -webkit-column-gap: 2rem;
       -moz-column-gap: 2rem;
            grid-column-gap: 2rem;
            column-gap: 2rem;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    margin: 1rem;
    margin-top: 4rem!important;
}
.event {
    font-size: 1.15rem;
    width: 100%;
    padding: 10px 10px 35px;
    margin-bottom: 10px;
    text-align: left;
    border-radius: 20px;
    position: relative;
    color: #fff;
    background-image: linear-gradient(to right top, #215599, #1e549e, #1c54a2, #1b53a7, #1c52ab, #1951b0, #184fb5, #194db9, #184abf, #1b47c5, #2043ca, #283ecf);
}
.link-div {
    text-align: center;
}
.link {
    color: white;
    font-size: 1.15rem;
}
.button-div {
    text-align: center;
}
.details-btn {
    font-weight: 600;
    font-size: .85rem;
    margin-top: 1rem;
    padding: 10px 62px;
    border-radius: 20px;
    color: black;
    cursor: pointer;
    text-transform: uppercase;
    border: none;
    background-color: white;
}
.details-btn:hover {
    transition: .25s;
    background-color: rgb(196, 196, 196);
}
.details {
    font-size: 1.05rem;
    margin-bottom: 2rem;
}
.name {
    font-weight: 600;
    text-align: center;
}
.group-name {
    text-transform: uppercase;
    font-size: 14px;
}
.going, .visibility {
    font-style: italic;
}
.description {
    overflow-x: hidden;
}
.numberOfEvents {
    width: 200px;
    margin: 10px auto;
    text-align: left;
}
.button-wrapper {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 1fr 20px 1fr 20px 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
    padding: 20px;
    font-family: sans-serif;
}
.Alert {
    text-align: center;
    width: 100%;
    margin-bottom: 1rem;
}
.data-vis-wrapper {
    margin: 4rem;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
    min-width: 100%;
    display: flex;
}
.recharts-surface {
    overflow: visible;
}
.login-button {
    color: #494949;
    text-transform: uppercase;
    text-decoration: none;
    background: #ffffff;
    padding: 20px;
    border: 4px solid #494949;
    display: inline-block;
    transition: all 0.4s ease 0s;
    min-width: 10rem;
    margin: 1rem;
}
.google-btn {
    width: 184px;
    height: 42px;
    margin: 1rem;
    background-color: #4285f4;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
    background: #1669f2;
}
.google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: #fff;
}
.google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: -8px;
    width: 18px;
    height: 18px;
}
@media only screen and (min-width: 992px) {
    .data-vis-wrapper {
        flex-direction: row;
   }
    .data-vis-wrapper .recharts-responsive-container {
        flex-basis: 50%;
        max-width: 50%;
        min-width: auto;
        margin: 0 auto;
   }
}
@media only screen and (max-width: 900px) {
    .EventList {
        display: -ms-grid;
        display: grid;
        -ms-grid-columns: 1fr 2rem 1fr;
        grid-template-columns: 1fr 1fr;
        -webkit-column-gap: 2rem;
           -moz-column-gap: 2rem;
                grid-column-gap: 2rem;
                column-gap: 2rem;
   }
}
@media only screen and (max-width: 750px) {
    .EventList {
        display: -ms-grid;
        display: grid;
        -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
        -webkit-column-gap: 2rem;
           -moz-column-gap: 2rem;
                grid-column-gap: 2rem;
                column-gap: 2rem;
   }
}
@media only screen and (max-width: 525px) {
  .data-vis-wrapper {
    margin: 3rem;
  }
}
@media only screen and (max-width: 500px) {
    .logo {
        width: 100px;
   }
   .data-vis-wrapper {
    margin: 1rem;
  }
}
@media only screen and (max-width: 425px) {
  .data-vis-wrapper {
    margin: 0rem;
  }
}
/* Make clicks pass-through */
#nprogress {
    pointer-events: none;
  }
  
  #nprogress .bar {
    background: #29d;
  
    position: fixed;
    z-index: 1031;
    top: 0;
    left: 0;
  
    width: 100%;
    height: 2px;
  }
  
  /* Fancy blur effect */
  #nprogress .peg {
    display: block;
    position: absolute;
    right: 0px;
    width: 100px;
    height: 100%;
    box-shadow: 0 0 10px #29d, 0 0 5px #29d;
    opacity: 1.0;
  
    -webkit-transform: rotate(3deg) translate(0px, -4px);
            transform: rotate(3deg) translate(0px, -4px);
  }
  
  /* Remove these to get rid of the spinner */
  #nprogress .spinner {
    display: block;
    position: fixed;
    z-index: 1031;
    top: 15px;
    right: 15px;
  }
  
  #nprogress .spinner-icon {
    width: 18px;
    height: 18px;
    box-sizing: border-box;
  
    border: solid 2px transparent;
    border-top-color: #29d;
    border-left-color: #29d;
    border-radius: 50%;
  
    -webkit-animation: nprogress-spinner 400ms linear infinite;
            animation: nprogress-spinner 400ms linear infinite;
  }
  
  .nprogress-custom-parent {
    overflow: hidden;
    position: relative;
  }
  
  .nprogress-custom-parent #nprogress .spinner,
  .nprogress-custom-parent #nprogress .bar {
    position: absolute;
  }
  
  @-webkit-keyframes nprogress-spinner {
    0%   { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  @keyframes nprogress-spinner {
    0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
